const login = async (username, password) => {
    try {
      const response = await fetch("http://localhost:5000/api/auth/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ username, password }), // Send username and password as JSON
      });
  
      const data = await response.json(); // Parse the JSON response
  
      if (response.ok) {
        // If login is successful
        localStorage.setItem("token", data.token); // Store the token in local storage
        console.log("Login successful:", data.token);
        return data.token; // Return token or any additional data if needed
      } else {
        // If login fails, handle the error
        console.error("Login failed:", data.message);
        alert(`Login failed: ${data.message}`); // Optional: Show an alert for better UX
        return null; // Return null or any error indication
      }
    } catch (error) {
      // Catch any unexpected errors (e.g., network issues)
      console.error("An error occurred:", error);
      alert("An error occurred during login. Please try again."); // Optional: Show alert
    }
  };
  